import { useState, useCallback, useMemo, useEffect } from "react";
import { ymdSlashDateFormat } from "../../Utils/Functions/dateUtility";
import dayjs from "dayjs";

export const dayName = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
export const monthName = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

export const monthPage = {
  0: 4,
  1: 3,
  2: 4,
  3: 3,
  4: 4,
  5: 3,
  6: 4,
  7: 4,
  8: 3,
  9: 4,
  10: 3,
  11: 4,
};

export const getYearList = () => {
  let max = new Date().getFullYear();
  let min = max - 70;
  let years = [];

  for (let i = max + 30; i >= min; i--) {
    years.push(i);
  }
  return years.reverse();
};

const dayCountInWeek = [1, 2, 3, 4, 5, 6, 0];

export const getToday = () => {
  let d = new Date();
  return d;
};
export const getDefaultToday = (val) => {
  // console.info("dfdfg", val);
  let mod = ymdSlashDateFormat(val);
  // console.info("mod", mod);

  let d = new Date(mod);
  // console.info("d", d);

  return d;
};
export const getDateFormat = (args) => {
  let argsSplit = args?.split("-");
  return new Date(`${argsSplit[2]}-${argsSplit[1]}-${argsSplit[0]}`);
};
export const getCompareToday = (args, isFormat) => {
  let argsSplit;
  if (isFormat) argsSplit = dayjs(args)?.format("YYYY-MM-DD");
  else argsSplit = ymdSlashDateFormat(args);
  let dDate = new Date();
  let argsSplit1 = dayjs(dDate)?.format("YYYY-MM-DD");
  let argDate = new Date(`${argsSplit}`);
  let argDate1 = new Date(`${argsSplit1}`);
  if (argDate.getTime() === argDate1.getTime()) {
    return true;
  } else {
    return false;
  }
};
const getDateModify = (dt) => {
  let sp = dt?.split("-");
  let d = sp?.[0] > 9 ? sp?.[0] : `0${sp?.[0]}`;
  let m = sp?.[1] > 9 ? sp?.[1] : `0${sp?.[1]}`;
  return `${d}/${m}/${sp?.[2]}`;
};

export const getCompareDate = (args, args1) => {
  if (args === getDateModify(args1)) {
    return true;
  } else {
    return false;
  }
};

const currentMonthLastDate = (args) => {
  let argDate = new Date(args);
  return new Date(argDate.getFullYear(), argDate.getMonth() + 1, 0);
};
const previousMonthLastDate = (args) => {
  let argDate = new Date(args);
  return new Date(argDate.getFullYear(), argDate.getMonth(), 0);
};
const currentDaysInMonth = (args) => {
  return currentMonthLastDate(args).getDate();
};
const previousDaysInMonth = (args) => {
  return previousMonthLastDate(args).getDate();
};
const currentFirstDayInMonth = (args) => {
  let argDate = new Date(args);
  let d = new Date(argDate.getFullYear(), argDate.getMonth(), 1);
  return d.getDay();
};
const getStartingPoint = (args) => {
  return dayCountInWeek.indexOf(currentFirstDayInMonth(args));
  // return dayCountInWeek.indexOf(currentFirstDayInMonth(args)) + 1;
};
const getPreviousMonthStartingPoint = (args) => {
  return previousDaysInMonth(args) - getStartingPoint(args);
};

const cRows = 7;
const cCols = 8;

const getCalendarRows = (args, argsPrevPoint) => {
  let cCounter = 1;
  let nCounter = 1;
  let argDate = new Date(args);

  const calendarRows = {};

  for (let i = 1; i < cRows; i++) {
    calendarRows[i] = [];

    for (let j = 1; j < cCols; j++) {
      if (i === 1) {
        if (j <= getStartingPoint(args) + 1) {
          // if (j < getStartingPoint(args) + 1) {
          let mt = argDate.getMonth() + 1;
          let render_day = `${argDate.getFullYear()}-${
            mt > 9 ? mt : `0${mt}`
          }-${cCounter > 9 ? cCounter : `0${cCounter}`}`;
          let rd_day = new Date(render_day);
          calendarRows[i] = [
            ...calendarRows[i],
            {
              type: "PREVIOUS",
              date: `${argsPrevPoint}-${
                argDate.getMonth() === 0 ? 12 : argDate.getMonth()
              }-${
                argDate.getMonth() === 0
                  ? argDate.getFullYear() - 1
                  : argDate.getFullYear()
              }`,
              value: argsPrevPoint,
              day: dayName[rd_day.getDay()],
            },
          ];
          ++argsPrevPoint;
        } else {
          let mt = argDate.getMonth() + 1;
          let render_day = `${argDate.getFullYear()}-${
            mt > 9 ? mt : `0${mt}`
          }-${cCounter > 9 ? cCounter : `0${cCounter}`}`;
          let rd_day = new Date(render_day);
          calendarRows[i] = [
            ...calendarRows[i],
            {
              type: "CURRENT",
              // classes: "CURRENT",
              date: `${cCounter}-${
                argDate.getMonth() + 1
              }-${argDate.getFullYear()}`,
              value: cCounter,
              day: dayName[rd_day.getDay()],
            },
          ];
          cCounter++;
        }
      } else if (i > 1 && cCounter < currentDaysInMonth(args) + 1) {
        let mt = argDate.getMonth() + 1;
        let render_day = `${argDate.getFullYear()}-${mt > 9 ? mt : `0${mt}`}-${
          cCounter > 9 ? cCounter : `0${cCounter}`
        }`;
        let rd_day = new Date(render_day);
        calendarRows[i] = [
          ...calendarRows[i],
          {
            type: "CURRENT",
            date: `${cCounter}-${
              argDate.getMonth() + 1
            }-${argDate.getFullYear()}`,
            value: cCounter,
            day: dayName[rd_day.getDay()],
          },
        ];
        cCounter++;
      } else {
        let mt = argDate.getMonth() + 1;
        let render_day = `${argDate.getFullYear()}-${mt > 9 ? mt : `0${mt}`}-${
          cCounter > 9 ? cCounter : `0${cCounter}`
        }`;
        let rd_day = new Date(render_day);
        calendarRows[i] = [
          ...calendarRows[i],
          {
            type: "NEXT_MONTH",
            date: `${nCounter}-${
              argDate.getMonth() + 2 === 13 ? 1 : argDate.getMonth() + 2
            }-${
              argDate.getMonth() + 2 === 13
                ? argDate.getFullYear() + 1
                : argDate.getFullYear()
            }`,
            value: nCounter,
            day: dayName[rd_day.getDay()],
          },
        ];
        nCounter++;
      }
    }
  }
  return calendarRows;
};

export const useCalendar = (defaultDate) => {
  const [selectedDate, setSelectedDate] = useState(getToday());

  useEffect(() => {
    if (defaultDate) {
      setSelectedDate(getDefaultToday(defaultDate));
    }
  }, [defaultDate]);
  const argsPrevPoint = getPreviousMonthStartingPoint(selectedDate);
  const generateCalendar = useMemo(
    () => getCalendarRows(selectedDate, argsPrevPoint),
    [selectedDate, argsPrevPoint]
  );
  // const generateCalendar = getCalendarRows(selectedDate, argsPrevPoint);
  const getPreviousMonth = useCallback(() => {
    setSelectedDate(
      (prevValue) =>
        new Date(prevValue.getFullYear(), prevValue.getMonth() - 1, 1)
    );
  }, []);
  const getNextMonth = useCallback(() => {
    setSelectedDate(
      (prevValue) =>
        new Date(prevValue.getFullYear(), prevValue.getMonth() + 1, 1)
    );
  }, []);
  const getSelectedDate = useCallback((data) => {
    let d_f = data?.split("-");
    let argDate = new Date(`${d_f[2]}-${d_f[1]}-${d_f[0]}`);
    setSelectedDate(argDate);
  }, []);

  return {
    selectedDate,
    setSelectedDate,
    getPreviousMonth,
    getNextMonth,
    getSelectedDate,
    generateCalendar,
    getToday,
    dayName,
    monthName,
    getCompareDate,
    monthPage,
  };
};
